/**
 * @file Copyright.tsx
 * @description 加密的版权标识组件
 * @author Arran
 */

'use client'

import { useEffect, useState } from 'react'

// Base64 编码的版权文本和链接（可以多次编码增加混淆）
const _0x1a2b = 'QXJjaGl0ZWN0dXJhbCBkcml2ZXIgaXMgYmFzZWQgb24g' // "Architectural driver is based on "
const _0x2b3c = 'WHMtYmxvZy4=' // "Xs-blog."
const _0x3c4d = 'aHR0cHM6Ly9naXRodWIuY29tL3N1MTY4ODgvWHMtYmxvZw==' // GitHub URL

// 解码函数（混淆）
const _0xDecode = (s: string): string => {
  try {
    return atob(s)
  } catch {
    return ''
  }
}

// 验证函数（防止篡改）
const _0x5e6f = (): boolean => {
  const _t1 = _0xDecode(_0x1a2b)
  const _t2 = _0xDecode(_0x2b3c)
  return _t1.length > 0 && _t2.includes('Xs-blog')
}

export default function Copyright() {
  const [_text1, _setText1] = useState<string>('')
  const [_text2, _setText2] = useState<string>('')
  const [_link, _setLink] = useState<string>('')
  const [_valid, _setValid] = useState<boolean>(false)

  useEffect(() => {
    // 延迟加载，防止直接查看
    const timer = setTimeout(() => {
      if (_0x5e6f()) {
        _setText1(_0xDecode(_0x1a2b))
        _setText2(_0xDecode(_0x2b3c))
        _setLink(_0xDecode(_0x3c4d))
        _setValid(true)
      }
    }, 100)

    return () => clearTimeout(timer)
  }, [])

  // 防止删除检测
  useEffect(() => {
    const checkIntegrity = () => {
      const element = document.querySelector('[data-copyright]')
      if (!element && _valid) {
        console.warn('Copyright notice must not be removed.')
      }
    }

    const interval = setInterval(checkIntegrity, 5000)
    return () => clearInterval(interval)
  }, [_valid])

  // 双击清除所有缓存和数据
  const handleDoubleClick = async () => {
    try {
      // 清除 localStorage
      localStorage.clear()

      // 清除 sessionStorage
      sessionStorage.clear()

      // 清除所有 cookies
      document.cookie.split(';').forEach(cookie => {
        const name = cookie.split('=')[0].trim()
        document.cookie = `${name}=;expires=Thu, 01 Jan 1970 00:00:00 GMT;path=/`
      })

      // 清除 Cache Storage
      if ('caches' in window) {
        const cacheNames = await caches.keys()
        await Promise.all(cacheNames.map(name => caches.delete(name)))
      }

      // 清除 IndexedDB
      if ('indexedDB' in window) {
        const databases = await indexedDB.databases?.() || []
        databases.forEach(db => {
          if (db.name) indexedDB.deleteDatabase(db.name)
        })
      }

      alert('清理成功全部数据！')

      // 刷新页面
      window.location.reload()
    } catch (error) {
      alert('清理成功全部数据！')
      window.location.reload()
    }
  }

  if (!_valid || !_text1) return null

  return (
    <div
      data-copyright="true"
      className="text-[10px] md:text-xs text-gray-500 text-center select-none"
      style={{
        userSelect: 'none',
        WebkitUserSelect: 'none',
        MozUserSelect: 'none'
      }}
      onContextMenu={(e) => e.preventDefault()}
    >
      <span onDoubleClick={handleDoubleClick} style={{ cursor: 'default' }}>
        {_text1}
      </span>
      <a
        href={_link}
        target="_blank"
        rel="noopener noreferrer"
        className="text-primary-500 hover:text-primary-600 transition-colors"
        style={{
          userSelect: 'none',
          WebkitUserSelect: 'none',
          MozUserSelect: 'none'
        }}
      >
        {_text2}
      </a>
    </div>
  )
}
